html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #030303;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.4rem;
}

.boton-negro {
  background-color: #030303;
  color: #F2F2F2;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 10px;
}
.boton-negro:hover {
  background-color: black;
}

.boton-negro:hover {
  background-color: #86cd25;
  color: #F2F2F2;
}

.alinear-derecha {
  display: flex;
  justify-content: center;
  padding-bottom: 5rem;
}

/*seccion de la carpeta componentes UI*/
.no-webp .inicio {
  background-image: url("../img/elcable.jpg");
}

.webp .inicio {
  background-image: url("../img/elcable.jpg");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header .titulo {
  color: #F2F2F2;
  font-weight: 700;
  text-align: left;
  max-width: 60rem;
  line-height: 2;
}

.barranav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barranav {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #F2F2F2;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.8s ease;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #86cd25;
  cursor: pointer;
}

@media (min-width: 768px) {
  .campus {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
  }
}
.entrada-campus {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 2rem;
  border-radius: 10px;
  background-color: #F2F2F2;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
}

.imagen {
  width: 100%;
  max-width: 300px;
  margin-right: 2rem;
  margin-left: 2rem;
}

h4 {
  text-align: center;
  margin: 2rem;
  color: #030303;
}

.contenedor-ms {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contenedor-ms {
    flex-direction: row;
  }
}

.footer {
  background-color: #333333;
  color: #F2F2F2;
  padding: 1rem;
  text-align: center;
}
.footer a {
  color: #F2F2F2;
  text-decoration: none;
}

.imagen-footer {
  width: 50px;
  height: 50px;
  margin: 0 3rem;
}

.imagens-footer {
  display: flex;
  margin-left: 5rem;
}

.contenedor-footer {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contenedor-footer {
    justify-content: space-between;
    flex-direction: row;
    padding: 3rem;
  }
}

@media (min-width: 768px) {
  .cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    margin: 3rem;
  }
}
h2 {
  text-align: center;
  margin: 2rem;
  color: #86cd25;
  font-weight: 700;
}

.imagen-sitio {
  border-radius: 20px;
}

.card {
  border-radius: 20px;
  background-color: #F2F2F2;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
}

p {
  text-align: justify;
  padding: 2rem;
}

h3 {
  text-align: center;
  font-weight: 700;
  margin: 2rem;
}

.contenedor-aside {
  display: flex;
  flex-direction: column;
  background-color: #F2F2F2;
  border-radius: 40px;
  padding: 5rem;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .contenedor-aside {
    flex-direction: row;
    width: 40rem;
    margin: 5rem;
  }
}

@media (min-width: 768px) {
  .pagina-campus {
    margin: 0 auto;
    padding: 10rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
  }
}
.pagina-campus {
  padding: 5rem;
}